
body {
  display: flex;
  justify-content: center;
  font-family: 'Inter', sans-serif;
}
.top {
  max-width: 350px;
  height: 270px;
  margin: 0;
}

.top-image {
  width: 350px;
  margin: 0;
}

.container {
  max-width: 350px;
  background-color: #1A1B21
  ;
  margin: 0;
  color: white;
  padding-bottom: 2em;
}

.title-name {
  padding-top: 1em;
  text-align: center;
  font-size: 25px;
  margin: 0;
}

.title-work {
  text-align: center;
  color: #F3BF99;
  font-size: 13px;

}

.title-site {
  text-align: center;
  font-size: 10px;

}

.email-btn {
  background-color: #D1D5DB;
  border-radius: 5px;
  margin: 0;
  color: #374151;
  text-align: center;
  width: 30%;
  font-size: 14px;
  padding: .5em 0;
}

.linked-btn {
  background-color: #5093E2;
  width: 30%;
  border-radius: 5px;
  text-align: center;
  font-size: 14px;
  padding: .5em 0;
}

.btn-container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  padding-bottom: 1em;
}

.paragraph-container {
  width: 300px;
  margin: 0 auto;
}

.paragraph-container > h3 {
  color: #F5F5F5;
  font-size: 16px;
}

.paragraph-container > p {
  color: #DCDCDC;
  font-size: 12px;
  line-height: 27px;
  margin: 0;
}

.end {
  background-color: #161619;
  text-align: center;
  display: flex;
  margin: 0;
  justify-content: space-evenly;
  padding: 1em;

}

.end>img {
  padding: 1em;
  width: 15px;
}

svg {
  margin-right: 7px;
}


